\n}\n\n:focus {\n    outline: none;\n}\n\n[hidden] {\n    display: none;\n}\n\n.nobr {\n    white-space: nowrap;\n}\n\ni,\nem,\ncode {\n    font-style: normal;\n}\n\nb,\nstrong {\n    font-weight: bold;\n}\n\nsub,\nsup {\n    font-size: 75%;\n    line-height: 0;\n    position: relative;\n    vertical-align: baseline;\n}\n\nsup {\n    top: -0.5em;\n}\n\nbutton,\ninput,\nselect,\ntextarea {\n    font-family: inherit;\n    /* 1 */\n    font-size: 100%;\n    /* 2 */\n    margin: 0;\n    /* 3 */\n}\n\ninput:focus,\nselect:focus,\ntextarea:focus,\nbutton:focus, button:active {\n    outline: none;\n}\n\ntable {\n    border-collapse: collapse;\n    border-spacing: 0;\n}\n\n.clearfix:after {\n    visibility: hidden;\n    display: block;\n    content: \" \";\n    clear: both;\n}\n\n.clearfix {\n    zoom: 1;\n}\n\n@media print {\n    body,\n    #main,\n    #content {\n        color: #000;\n    }\n\n    a,\n    a:link,\n    a:visited {\n        color: #000;\n        text-decoration: none;\n    }\n}\n\n/** font style **/\n.font-bold {\n    font-weight: bold;\n}\n\n.font-color-red {\n    color: #d1434a;\n}\n\n.font-color-orange {\n    color: #ee7a23;\n}\n\n.font-color-gray {\n    color: #605F5F;\n}\n\n.font-color-blue {\n    color: #2bc4e2;\n}\n\n.font-color-white {\n    color: #fff;\n}\n\n.font-color-black {\n    color: #333;\n}\n\n.font-underline {\n    text-decoration: underline;\n}\n\n.font-link:hover {\n    opacity: .7;\n}\n\n.font-size-12 {\n    font-size: 12px;\n}\n\n.font-size-16 {\n    font-size: 16px;\n}\n\n.font-size-18 {\n    font-size: 18px;\n}\n\n.font-size-20 {\n    font-size: 20px;\n}\n\n.font-size-24 {\n    font-size: 24px;\n}\n\n.font-size-32 {\n    font-size: 32px;\n}\n\n.font-size-36 {\n    font-size: 36px;\n}\n\n.font-size-48 {\n    font-size: 48px;\n}\n\n.font-size-60 {\n    font-size: 60px;\n}\n\n.font-size-5x {\n    font-size: .5em;\n}\n\n.font-size-8x {\n    font-size: .8em;\n}\n\n.font-size-12x {\n    font-size: 1.2em;\n}\n\n.font-size-15x {\n    font-si