: 54px;\n    padding: 0 10px;\n}\n\n.btn, .btn[href] {\n    display: inline-block;\n    text-align: center;\n    font-size: 14px;\n    font-family: \"moderat\", sans-serif;\n    font-weight: bold;\n    -webkit-transition: all .3s ease-out;\n    transition: all .3s ease-out;\n    border: 1px solid #d1434a;\n    color: #d1434a;\n    text-transform: uppercase;\n    letter-spacing: .25em;\n    white-space: nowrap;\n}\n\n.btn:hover, .btn[href]:hover {\n    background-color: #ffe5e6;\n    -webkit-transition: all .3s ease-out;\n    transition: all .3s ease-out;\n}\n\n.btn--red, .btn--red[href] {\n    background-color: #d1434a;\n    border-color: #d1434a;\n    color: #fff;\n}\n\n.btn--red:hover, .btn--red[href]:hover {\n    background-color: #f16f75;\n    border-color: #f16f75;\n    color: #fff;\n}\n\n.btn--m {\n    height: 40px;\n    line-height: 40px;\n}\n\n.btn--s {\n    height: 35px;\n    line-height: 35px;\n    font-size: 12px;\n    letter-spacing: .1em;\n}\n\n.btn-line2 {\n    white-space: normal;\n    line-height: 1em;\n    vertical-align: top;\n}\n\n.up-down-btn {\n    color: #ee7a23;\n}\n\n.i-up-down {\n    position: relative;\n    display: inline-block;\n    width: 14px;\n    height: 6px;\n    vertical-align: middle;\n    z-index: 2;\n    -webkit-transform: translateY(2px);\n    -ms-transform: translateY(2px);\n    transform: translateY(2px);\n    -webkit-transition: -webkit-transform .3s ease-out;\n    transition: -webkit-transform .3s ease-out;\n    transition: transform .3s ease-out;\n    transition: transform .3s ease-out, -webkit-transform .3s ease-out;\n}\n\n.i-up-down i {\n    position: absolute;\n    top: 0;\n    width: 8px;\n    height: 1px;\n    background: #ee7a23;\n}\n\n.i-up-down .i-up-down-l {\n    left: 0;\n    -webkit-transform: rotate(40deg);\n    -ms-transform: rotate(40deg);\n    transform: rotate(40deg);\n    -webkit-transition: -webkit-transform .3s ease-out;\n    transition: -webkit-transform .3s ease-out;\n    transition: transform .3s ease-out;\n    transition: transform .3s ease-out, -webkit-transform .3s ease-out;\n}\n\n.i-up-down .i-up-down-r {\n    right: 0;\n    -webkit-transform: rotate(-40deg);\n    -ms-transform: rotate(-40deg);\n    transform: rotate(-40deg);\n    -webkit-transition: all .3s ease-out;\n    transition: all .3s ease-out;\n}\n\n.select-self {\n    position: relative;\n    display: inline-block;\n    cursor: pointer;\n}\n\n.select-self-area {\n    width: auto;\n    background: #f0f0f0;\n    border-radius: 3px;\n    min-width: 50px;\n    max-width: 240px;\n    height: 30px;\n    line-height: 30px;\n}\n\n.select-self-area .select-ipt {\n    display: block;\n    min-width: 56px;\n    height: 100%;\n    padding: 0 25px 0 10px;\n    border: 0;\n    color: #605F5F;\n    text-align: left;\n    font-size: 16px;\n    overflow: hidden;\n}\n\n.select-self-area .select-btn {\n    position: absolute;\n    right: 0;\n    top: 0;\n    width: 18px;\n    height: 100%;\n    background: #fff;\n    border: 1px solid #ccc;\n    border-radius: 3px;\n    text-align: center;\n}\n\n.select-self-area .select-btn .i-up-down {\n    width: 8px;\n    top: 0;\n    left: 0;\n}\n\n.select-self-area .select-btn .i-up-down i {\n    width: 5px;\n    height: 2px;\n    background-color: #605F5F;\n}\n\n.select-self-open {\n    border-radius: 3px 3px 0 0;\n}\n\n.select-self-open .select-options {\n    display: block;\n}\n\n.select-self-open .i-up-down .i-up-down-l {\n    left: 0;\n    -webkit-transform: rotate(-40deg);\n    -ms-transform: rotate(-40deg);\n    transform: rotate(-40deg);\n    -webkit-transition: -webkit-transform .3s ease-out;\n    transition: -webkit-transform .3s ease-out;\n    transition: transform .3s ease-out;\n    transition: transform .3s ease-out, -webkit-transform .3s ease-out;\n}\n\n.select-self-open .i-up-down .i-up-down-r {\n    right: 0;\n    -webkit-transform: rotate(40deg);\n    -ms-transform: rotate(40deg);\n    transform: rotate(40deg);\n    -webkit-transition: all .3s ease-out;\n    tra